export default {
	name: "share",
	data() {
		return {
 
		}
	},
	onLoad: function() {
		wx.showShareMenu({
			withShareTicket: true,
			menus: ["shareAppMessage", "shareTimeline"]
		})
	},
	//
	onShareAppMessage(res) {
		let that = this;
		if (res.from === 'button') {
			let obj = res.target.dataset.obj //  button  data-obj
			//
			// let path = `/` + that.$scope.route + `?item=` + that.$scope.options.item;
			let path = ''
			return {
				title: `${obj.filename}`,
				path: path,
				imageUrl: ''
			};
		}
		// 
		if (res.from === 'menu') {
			return {
				title: 'Ai',
				path: '',
				imageUrl: ''
			};
		}
	},
	// 
	onShareTimeline() {
		return {
			title: 'Ai',
			path: '',
			imageUrl: ''
		};
	},
	methods: {
 
	}
}